import getArtboard from './artboard';
import getImages from './images';
import getSVGs from './svgs';
const getStore = (selectedArtboard, sketch) => {
    // get final store items
    const artboard = getArtboard(selectedArtboard, sketch);
    const images = getImages(artboard.layers, sketch);
    const svgs = getSVGs(artboard.layers, sketch);
    const notes = {};
    // remove duplicate artboard
    artboard.remove();
    // run callback
    return {
        artboard,
        images,
        svgs,
        notes
    };
};
export default getStore;
