import { getFileContent } from '../export';
const getOddShapePathSVGs = (layers, sketch, svgs = []) => {
    layers.forEach((layer) => {
        if (layer.type === 'ShapePath') {
            const hasOpenPath = !layer.closed;
            const notRectangle = layer.shapeType !== 'Rectangle';
            const notOval = layer.shapeType !== 'Oval';
            const isOddShape = notRectangle && notOval;
            if (hasOpenPath || isOddShape) {
                // create svg in temp directory
                sketch.export(layer, {
                    formats: 'svg',
                    // @ts-ignore
                    output: NSTemporaryDirectory(),
                    ['use-id-for-name']: true,
                    compact: true,
                    overwriting: true
                });
                // get new svg path
                // @ts-ignore
                const filePath = `${NSTemporaryDirectory()}${layer.id}.svg`;
                // read contents of svg
                const svgContent = getFileContent(filePath);
                // set contents in svgs
                svgs.push({
                    id: layer.id,
                    svg: `${svgContent}`
                });
            }
        }
    });
    return svgs;
};
const getShapeSVGs = (layers, sketch, svgs = []) => {
    layers.forEach((layer) => {
        if (layer.type === 'Shape') {
            // create svg in temp directory
            sketch.export(layer, {
                formats: 'svg',
                // @ts-ignore
                output: NSTemporaryDirectory(),
                ['use-id-for-name']: true,
                compact: true,
                overwriting: true
            });
            // get new svg path
            // @ts-ignore
            const filePath = `${NSTemporaryDirectory()}${layer.id}.svg`;
            // read contents of svg
            const svgContent = getFileContent(filePath);
            // set contents in svgs
            svgs.push({
                id: layer.id,
                svg: `${svgContent}`
            });
        }
    });
    return svgs;
};
const getSVGs = (layers, sketch) => {
    let shapeSvgs = getShapeSVGs(layers, sketch);
    let oddShapePathSvgs = getOddShapePathSVGs(layers, sketch);
    return [...shapeSvgs, ...oddShapePathSvgs];
};
export default getSVGs;
